@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&amp;display=swap');

* {
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

html, body {
  height: 100%;
  font-size: 16px;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(-45deg, #fda085 0%, #f6d365 100%);
  font-family: 'Rubik', sans-serif;
}

h1 {
  font-size: 1.25rem;
  margin: 0 0 2rem 0;
  text-align: center;
  color: rgba(255,255,255, 0.95);
  text-shadow: 0 0 0.0625rem rgba(0, 0, 0, 0.04), 0 0.0625rem 0.25rem rgba(0, 0, 0, 0.12);
}

nav {
  background: rgba(255,255,255, 0.95);
  border-radius: 0.5rem;
  box-shadow: 0 0 0 0.0625rem rgba(0, 0, 0, 0.04), 0 0.0625rem 0.25rem 0 rgba(0, 0, 0, 0.12), #eeeeee 0 0 0 0.0625rem inset;
  padding: 0 0.5rem;
}

nav ul {
  display: grid;
  grid-template-columns: repeat(4, 172px);
  list-style-type: none;
  padding: 0;
  margin: 0;
}

nav ul li {
  display: inline-block;
  text-align: center;
}

nav ul li a {
  display: block;
  font-size: 1.25rem;
  color: #3e3e3e;
  text-decoration: none;
  padding: 1rem 1rem 0.5rem 1rem;
  cursor: pointer;
}

nav ul li a:hover, nav ul li.active a {
  color: black;
}

hr {
  height: 0.25rem;
  width: 100%;
  background-color: #ff758c;
  border: none;
  transition: all 0.3s ease-in-out;
  margin: 0 0 0.25rem 0;
  border-radius: 0.25rem;
}

nav li.active:nth-child(2) ~ hr {
  margin-left: 100%;
}

nav li.active:nth-child(3) ~ hr {
  margin-left: 200%;
}

nav li.active:nth-child(4) ~ hr {
  margin-left: 300%;
}

nav li.active:nth-child(5) ~ hr {
  margin-left: 400%;
}

/* For better preview on mobile */
@media (max-width: 767px) {
  h1 {
    font-size: 1rem;
    margin: 0 0 1.5rem 0;
  }

  nav {
    transform: scale(0.75);
  }
}

@media (max-width: 575px) {
  h1 {
    font-size: 0.75rem;
    margin: 0 0 0.5rem 0;
  }

  nav {
    transform: scale(0.5);
  }
}
